import React from "react";
import { Input } from "ui/input";
import { PaymentAddress } from "lib/api";
import NumberFormat, { NumberFormatValues } from "react-number-format";
import { CurrencySelect } from "./currency-select";

interface ICurrencyInputComponentProps extends React.HTMLAttributes<HTMLElement> {
  onValueChange: (values: NumberFormatValues) => void;
  currency: PaymentAddress;
  onCurrencySelectClick: (currencyName: string) => void;
  renderAfter?: React.ReactNode;
  value?: string;
  label: React.ReactNode;
  error?: string;
  selectDisabled?: boolean;
  inputDisabled?: boolean;
  isLoading?: boolean;
  currencies?: PaymentAddress[];
}

export function CurrencyInput({
  onInput,
  value,
  label,
  onValueChange,
  onCurrencySelectClick,
  error,
  selectDisabled,
  inputDisabled,
  currency,
  currencies,
  isLoading = false,
  ...rest
}: ICurrencyInputComponentProps): JSX.Element {
  return (
    <Input
      label={label}
      error={error}
      slotAfterInput={
        <CurrencySelect
          currency={currency}
          onCurrencySelectClick={selectDisabled ? () => {} : onCurrencySelectClick}
          disabled={!!selectDisabled}
          options={currencies}
        />
      }
      {...rest}
    >
      {isLoading ? (
        <input disabled readOnly />
      ) : (
        <NumberFormat
          thousandSeparator={false}
          onValueChange={onValueChange}
          allowLeadingZeros={true}
          onInput={onInput}
          value={value}
          placeholder="0.00"
          readOnly={true}
        />
      )}
    </Input>
  );
}
