import "./expiry-timer.scss";

import { block } from "bem-cn";
import { useEffect, useState } from "react";
import dayjs from "dayjs";

const b = block("pay-with-near-pay-timer");

interface Props {
  expiryAt: string;
  onExpired: () => void;
}
export const ExpiryTimer: React.FC<Props> = (props) => {
  const { expiryAt, onExpired } = props;
  const [timeLeft, setTimeLeft] = useState(getTimeLeft(expiryAt));

  useEffect(() => {
    if (getTimeLeft(expiryAt) < 0) {
      onExpired();
    }

    const timer = setTimeout(() => {
      setTimeLeft(getTimeLeft(expiryAt));
    }, 1000);

    return () => clearTimeout(timer);
  }, [timeLeft, expiryAt, onExpired]);

  if (getTimeLeft(expiryAt) < 0) {
    return null;
  }

  return <div className={b()}>{msecToTimeFormat(timeLeft)}</div>;
};

const getTimeLeft = (expiryAt: string): number => {
  const expiryAtDayjs = dayjs(expiryAt);
  const curDayjs = dayjs.utc();

  const millisecondLeft = expiryAtDayjs.diff(curDayjs);

  return millisecondLeft;
};

const msecToTimeFormat = (ms: number): string => {
  const minutes = Math.floor(ms / (60 * 1000));
  const minutesMs = ms % (60 * 1000);
  const seconds = Math.floor(minutesMs / 1000);

  return `${minutes}:${seconds > 9 ? seconds : `0${seconds}`}`;
};
