import "./pay-widget.scss";

import { block } from "bem-cn";
import { NpLogoIcon } from "ui";
import classNames from "classnames";
import { useCallback, useEffect, useState } from "react";
import { QRCodeSVG } from "qrcode.react";

import { ProceedButton } from "components/proceed-button";
import { CurrencyInput } from "components/currency-input";
import { ExpiryTimer } from "components/expiry-timer";
import { PaymentAddress, TPayment } from "lib/api";
import { WidgetContainer } from "ui/widget-container/widget-container";
import { toast } from "lib/toast";
import { useNearContext } from "lib/near/near-context";

const b = block("pay-with-near-pay-widget");
const bDescription = block("pay-with-near-pay-widget-description");

interface Props {
  payment: TPayment;
  hasTrustWallet: (currency: PaymentAddress) => boolean;
  onTrustWallet: (currency: PaymentAddress) => void;
  onNearWallet: (currency: PaymentAddress) => void;
  onExpired: () => void;
}
export const PayWidget: React.FC<Props> = (props) => {
  const app = useNearContext();
  const { payment, onNearWallet, onExpired } = props;
  const { hasTrustWallet, onTrustWallet } = props;

  const [adresses, setAddresses] = useState<PaymentAddress[]>([]);
  const [currency, setCurrency] = useState<PaymentAddress>(() => payment.addresses[0]);

  useEffect(() => {
    const addresses = payment.addresses.filter((addr) => {
      if (payment.hide_usn === false) return true;
      return addr.token.toLowerCase() !== "usn";
    });

    setAddresses(addresses);
    setCurrency(addresses[0]);
  }, [payment]);

  let amount = `$${payment.display_amount_usd}`;
  if (payment.local_currency === "eur") {
    amount = `€${payment.display_amount_eur}`;
  }

  const changeCurrency = useCallback(
    (currency: string) => {
      const currentCurrency = adresses.find((item) => item.token === currency);
      setCurrency(currentCurrency ?? adresses[0]);
    },
    [adresses]
  );

  const handleCopyAdress = () => {
    navigator.clipboard.writeText(currency.address).then(() => {
      toast("The address has been copied to the clipboard");
    });
  };

  return (
    <WidgetContainer className={b()}>
      <div className={b("title")}>{payment.name}</div>
      <div className={b("description")}>{payment.description}</div>
      <div className={bDescription()}>
        <div>
          <div className={bDescription("label")}>Payment: </div>
          <div>{payment.payment_id}</div>
        </div>
        <div>
          <div className={bDescription("label")}>Recipient: </div>
          <div className={bDescription("address")}>{currency.address}</div>
        </div>
      </div>
      <div className={b("flexbox")}>
        <div className={b("amount")}>{amount}</div>
        <ExpiryTimer expiryAt={payment.expiry_at} onExpired={onExpired} />
      </div>

      <CurrencyInput
        label={"Amount to Pay"}
        value={currency.displayAmount}
        currency={currency}
        onValueChange={() => {}}
        onCurrencySelectClick={changeCurrency}
        selectDisabled={false}
        currencies={adresses}
        className="mb-4"
      />

      {app?.wallet && (
        <>
          <ProceedButton onClick={() => onNearWallet(currency)}>
            <div style={{ display: "flex", alignItems: "center", gap: 8 }}>Pay with {app?.wallet.metadata.name}</div>
          </ProceedButton>

          <ProceedButton onClick={() => app?.wallet?.signOut()}>Logout</ProceedButton>
          <div className={b("payment-address")}>
            <span style={{ flexShrink: 0 }}>You signed in</span>
            <span>{app.selector.store.getState().accounts.find((acc) => acc.active)?.accountId}</span>
          </div>
        </>
      )}

      {!app?.wallet && <ProceedButton onClick={() => onNearWallet(currency)}>Login with Near Selector</ProceedButton>}

      {hasTrustWallet(currency) && (
        <ProceedButton onClick={() => onTrustWallet(currency)}>Pay with Trust Wallet</ProceedButton>
      )}

      <div className={b("or")}>or</div>

      <div className={b("payment-qr-text")}>Tap on the qr code to copy the address</div>
      <div className={b("qr-code-container")} onClick={handleCopyAdress}>
        <QRCodeSVG value={`${currency.address}?amount=${currency.displayAmount}`} />
      </div>

      <div className={b("payment-method-text")}>
        Scan following&nbsp;QR code with your wallet or&nbsp;enter Account&nbsp;ID&nbsp;and amount manually.
      </div>

      <a href="http://nearpay.co" target="_blank" rel="noopener noreferrer" className={classNames(b("link-nearpay"))}>
        <span className={b("link-nearpay_gray")}>Powered by</span>
        {"  "}
        <NpLogoIcon className={b("link-nearpay_icon")} width="20" height="15" />
        {"  "}
        <span>NearPay</span>
      </a>
    </WidgetContainer>
  );
};
