import classNames from "classnames";
import React, {
  AnchorHTMLAttributes,
  ButtonHTMLAttributes,
  forwardRef,
} from "react";
import "./proceed-button.scss";

interface IProceedButtonComponentProps
  extends ButtonHTMLAttributes<HTMLButtonElement> {
  disabled?: boolean;
  loading?: boolean;
  loadingText?: string | React.ReactNode;
  withArrow?: boolean;
  icon?: React.ReactNode;
}
interface IProceedLinkComponentProps
  extends AnchorHTMLAttributes<HTMLAnchorElement> {
  disabled?: boolean;
  loading?: boolean;
  loadingText?: string | React.ReactNode;
  withArrow?: boolean;
}

function proceedAppearance({
  disabled,
  loading,
}: {
  disabled: boolean;
  loading: boolean;
}) {
  const stateClassName = disabled
    ? "ProceedButton--disabled"
    : loading
    ? "ProceedButton--loading"
    : "ProceedButton--active";

  return stateClassName;
}

export const ProceedLink = forwardRef<
  HTMLAnchorElement,
  IProceedLinkComponentProps
>(
  (
    {
      disabled = false,
      children,
      loading = false,
      className,
      withArrow = true,
      loadingText,
      href,
      ...rest
    },
    ref
  ) => {
    const stateClassName = proceedAppearance({ disabled, loading });
    return (
      <a
        ref={ref}
        className={classNames("ProceedButton", stateClassName, className)}
        href={disabled ? undefined : href}
        {...rest}
      >
        {loading && loadingText ? (
          <>
            <span></span>
            <span>{loadingText}</span>
            <span></span>
          </>
        ) : (
          <>
            <span></span>
            <span>{children}</span>
            <span></span>
          </>
        )}
      </a>
    );
  }
);

export const ProceedButton = forwardRef<
  HTMLButtonElement,
  IProceedButtonComponentProps
>(
  (
    {
      disabled = false,
      children,
      loading = false,
      className,
      withArrow = true,
      loadingText,
      icon,
      ...rest
    },
    ref
  ) => {
    const stateClassName = proceedAppearance({ disabled, loading });

    return (
      <button
        ref={ref}
        className={classNames("ProceedButton", stateClassName, className)}
        disabled={disabled}
        {...rest}
      >
        {loading && loadingText ? (
          <>
            <span></span>
            <span>{loadingText}</span>
            <span></span>
          </>
        ) : (
          <>
            <span></span>
            <span>{children}</span>
            <span></span>
          </>
        )}
        {icon && <div className={"ProceedButton__icon"}>{icon}</div>}
      </button>
    );
  }
);
