import classNames from "classnames";
import React from "react";

import "./input.scss";

interface IInputComponentProps
  extends React.LabelHTMLAttributes<HTMLLabelElement> {
  slotAfterInput?: React.ReactNode;
  slotAfterLabel?: React.ReactNode;
  slotRight?: React.ReactNode;
  label: React.ReactNode;
  error?: string;
  size?: "big" | "normal" | "small";
  textSize?: "" | "small" | "normal" | "big";
}

export function Input({
  label,
  slotAfterInput,
  slotRight,
  slotAfterLabel,
  children,
  error,
  size = "normal",
  textSize = "normal",
  className,
  placeholder,
  ...rest
}: IInputComponentProps): JSX.Element {
  if (slotAfterInput && slotRight) {
    throw Error("Only one of after input slots can be used");
  }

  return (
    <label
      className={classNames("Input", error ? "Input--hasError" : "", className)}
      {...rest}
    >
      <div className="flex">
        <div className="flex-grow">
          <div className={"Input__header"}>
            <div className={"Input__label"}>{label}</div>
            {slotAfterLabel}
          </div>
          <div className={"Input__wrapper"}>
            {typeof children === "function"
              ? children({ inputClass: "Input__input" })
              : children}
            {slotAfterInput ? (
              <div className={"Input__after"}>{slotAfterInput}</div>
            ) : null}
          </div>
        </div>
        {slotRight ? (
          <div className={"Input__afterCentered"}>{slotRight}</div>
        ) : null}
      </div>
      {error ? <div className={"Input__error"}>{error}</div> : null}
    </label>
  );
}
