import classNames from "classnames";
import { PaymentAddress } from "lib/api";
import { isMobileAndTablet } from "lib/userAgent";
import { HTMLAttributes, useCallback, useState } from "react";
import { NpDropdownArrowIcon } from "ui";
import { Icon } from "ui/icon";

import "./currency-select.scss";

interface ICurrencySelectComponentProps extends HTMLAttributes<HTMLDivElement> {
  currency: PaymentAddress;
  disabled: boolean;
  options?: PaymentAddress[];
  onCurrencySelectClick: (currencyName: string) => void;
}

export function CurrencySelect({
  currency,
  options,
  className,
  disabled,
  onCurrencySelectClick,
  ...rest
}: ICurrencySelectComponentProps): JSX.Element {
  const [isOpen, setOpen] = useState(false);
  const toggleOpen = useCallback(() => {
    setOpen((prev) => !prev);
  }, []);

  const chooseOption = useCallback(
    (value: string) => {
      toggleOpen();
      onCurrencySelectClick(value);
    },
    [toggleOpen, onCurrencySelectClick]
  );

  return (
    <div className={classNames("CurrencySelect", disabled ? "CurrencySelect--disabled" : "", className)} {...rest}>
      {isMobileAndTablet() ? (
        <>
          <Icon className={"CurrencySelect__icon"}>
            <img src={currency.icon} alt={currency.token} />
          </Icon>
          <select className={"CurrencySelect__select"} onChange={(event) => onCurrencySelectClick(event.target.value)}>
            {options?.map((option) => (
              <option key={option.token} value={option.token}>
                {option.token}
              </option>
            ))}
          </select>
        </>
      ) : (
        <>
          <div className={"CurrencySelect__container"}>
            <button className={"CurrencySelect__button"} onClick={toggleOpen}>
              <Icon className={"CurrencySelect__icon"}>
                <img src={currency.icon} alt={currency.token} />
              </Icon>
              <div className={"CurrencySelect__text"}>{currency.token}</div>
              <Icon size={20} className={"CurrencySelect__arrow"}>
                <NpDropdownArrowIcon />
              </Icon>
            </button>
            {isOpen && (
              <ul className={"CurrencySelect__list"}>
                {options?.map((option) => (
                  <li key={option.icon} className={"CurrencySelect__item"} onClick={() => chooseOption(option.token)}>
                    <Icon className={"CurrencySelect__icon"}>
                      <img src={option.icon} alt={option.token} />
                    </Icon>
                    {option.token}
                  </li>
                ))}
              </ul>
            )}
          </div>
        </>
      )}
    </div>
  );
}
