import classNames from "classnames";
import React, { HTMLAttributes } from "react";

import "./icon.scss";

export type TCreditCardIconName =
  | "amex"
  | "card"
  | "mastercard"
  | "unionpay"
  | "visa";

export type TCurrencyIconName =
  | "currency-btc"
  | "currency-usd"
  | "currency-near";

export type TIconName =
  | "arrow-up-circle"
  | "info-square"
  | "dropdown-arrow"
  | "arrow-right-square"
  | "near-pay-logo"
  | "spinner";

interface ICssTransform {
  rotate?: string;
}

interface IIconComponentProps extends HTMLAttributes<HTMLDivElement> {
  icon?: TIconName | TCurrencyIconName | TCreditCardIconName;
  renderIcon?: React.ReactNode;
  size?: number;
  transform?: ICssTransform;
}

function buildTransform(transform: ICssTransform): string {
  if (!transform) return "";

  const properties: string[] = [];

  for (const [property, value] of Object.entries(transform)) {
    properties.push(`${property}(${value})`);
  }

  return properties.join(",");
}

export function Icon({
  icon = "info-square",
  className,
  transform,
  size,
  children,
  ...rest
}: IIconComponentProps): JSX.Element {
  // const IconComponent = icons[icon];

  const transformProp = buildTransform(transform || {});

  return (
    <i
      className={classNames("pay-with-near-icon", className)}
      style={{ transform: transformProp, width: size, height: size }}
      {...rest}
    >
      {children}
      {/* {children ||
        (IconComponent && (
          <Suspense fallback={<span></span>}>
            <IconComponent />
          </Suspense>
        ))} */}
    </i>
  );
}
