import { isMobileAndTablet } from "lib/userAgent";
import { PaymentAddress } from "lib/api";
import { toast } from "lib/toast";

const useTrustWallet = () => {
  const isAvailable = (currency: PaymentAddress) => currency.token === "NEAR" && isMobileAndTablet();

  return {
    isAvailable,
    pay: async (address: PaymentAddress) => {
      if (isAvailable(address) === false) {
        toast("Trust Wallet is not support for current platform or currency");
        return;
      }

      const asset = "c397"; // NEAR ticker id
      const base = "https://link.trustwallet.com/send";
      window.location.href = `${base}?asset=${asset}&address=${address.address}&amount=${address.displayAmount}`;
    },
  };
};

export default useTrustWallet;
